body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* 导航栏 */
#root .MuiAppBar-colorPrimary {
  background-color: #885599;
  box-shadow: 0px 4px 10px 0px rgba(59, 21, 72, 0.3);
}

/* 默认提示条 */
#root .MuiAlert-filledInfo {
  background-color: #b287c0;
  box-shadow: 0px 3px 10px 0px rgba(84, 21, 105, 0.3);
}

/* 警告提示条 */
#root .MuiAlert-filledError {
  background-color: #e45263;
  box-shadow: 0px 4px 10px 0px rgba(221, 23, 37, 0.3);
}

/* 抽屉悬浮色 */
#root .MuiListItem-root:hover,
#root .MuiListItem-root:hover .MuiSvgIcon-root {
  color: #885599;
}

/* 抽屉选中色 */
.MuiListItem-root.Mui-selected {
  color: #885599;
  background-color: rgba(178, 135, 192, 0.2);
}

.MuiListItem-root.Mui-selected .MuiListItemIcon-root {
  color: #885599;
}

/* 抽屉滚动条 */
#root .MuiDrawer-paper::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #fff;
}
#root .MuiDrawer-paper::-webkit-scrollbar {
  width: 12px;
  background-color: #fff;
}
#root .MuiDrawer-paper::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #885599;
}

/* 回到顶部按钮 */
#root .MuiFab-secondary {
  background-color: #885599;
  box-shadow: 0px 4px 10px 0px rgba(59, 21, 72, 0.3);
}
